@import '../config/colors';
@import '../config/sizes';
@import '../config/typography';
@import '../mixins/button';
@import '../mixins/screen';
@import '../mixins/sr-only';
@import '../mixins/svg';
@import '../mixins/typography';

.nav {
    $self: &;

    --nav-link-color: #{ $c-blue--darker };
    --nav-link-border-color: currentColor;
    --nav-link-border-width: 2px;

    color: var(--nav-link-color);
    font-family: $font-inter;
    font-size: 1rem;
    line-height: 1.5;

    &__title {
        display: block;
        font-size: 1rem;
        font-weight: $weight-bold;
        line-height: 1.2;
        margin: 0 0 2rem;
    }

    &__list {
        display: block;
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &__item {
        + #{ $self }__item {
            margin-top: 0.5rem;
        }
    }

    &__link {
        cursor: pointer;
        text-decoration: none;

        &:hover,
        &:focus {
            #{ $self }__text {
                background-position-x: 0;
                background-size: 100% var(--nav-link-border-width);
                color: var(--footer-legal-hover-color, $c-white);
            }
        }

        &[disabled] {
            color: $c-grey;
            cursor: default;
            font-style: italic;

            #{ $self }__text {
                background-size: 0 0;
            }
        }
    }

    &__text {
        background: linear-gradient(var(--nav-link-border-color), var(--nav-link-border-color)) no-repeat 100% 100%;
        background-size: 0 var(--nav-link-border-width);
        display: inline-block;
        position: relative;
        transition:
            background-color 0.2s ease-in-out,
            background-size 0.2s ease-in-out,
            color 0.2s ease-in-out;
    }

    &--legal {
        font-size: 0.875rem;
        font-weight: $weight-medium;
        letter-spacing: 0.045em;

        &:focus,
        &:hover {
            --footer-legal-hover-color: #{ $c-white };
        }

        #{ $self }__list {
            align-items: center;
            display: flex;
            flex-direction: column;

            + #{ $self }__list {
                margin: 2rem 0 0;
            }
        }

        #{ $self }__item {
            + #{ $self }__item {
                margin: 0.5rem 0 0;
            }
        }

        #{ $self }__text {
            color: var(--footer-legal-color, $c-grey);
        }
    }

    @include screen($screen-simple) {
        &--legal {
            align-items: center;
            display: flex;
            justify-content: space-between;
            width: 100%;

            #{ $self }__list {
                flex-direction: row;
                gap: $p-gutter--large;
                height: 100%;

                + #{ $self }__list {
                    margin: 0;
                }
            }

            #{ $self }__item {
                + #{ $self }__item {
                    margin: 0;
                }
            }
        }
    }
}
