@import '../config/sizes';
@import '../mixins/container';
@import '../mixins/screen';
@import '../mixins/typography';

.text {
    $self: &;

    display: flex;
    flex-direction: column;
    gap: 3rem;

    &__col {
        align-items: flex-start;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        position: relative;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__title {
        @include style-heading-1;

        line-height: 1;
        margin: 0;
    }

    &__divider {
        border-top: 1px solid $c-blue;
    }

    @include screen($screen-simple) {
        align-items: stretch;
        display: flex;
        flex-direction: row;

        &__divider {
            border-right: 1px solid $c-blue;
            border-top: none;
            margin: (-$p-gutter) 0;
        }

        &--single {
            @include container(($w-section-container * 0.5));
        }

        &--double {
            #{ $self }__col {
                flex: 0 1 50%;
                max-width: 50%;
            }

            .button {
                margin-top: auto;
            }
        }
    }

    @include screen($screen-normal) {
        &--double {
            gap: 5rem;
        }
    }
}
