@import '../config/colors';
@import '../mixins/marginless-child';

.content {
    $self: &;

    --heading-color: #{ $c-blue--dark };

    @include marginless-child;

    color: var(--text-color); // Fix for color inheritance

    h2,
    h3,
    h4,
    h5,
    h6 {
        font-weight: $weight-bold;
        line-height: 1.875;
        margin-bottom: 0;
    }

    h2 {
        font-size: 1.25rem;
        margin: 3rem 0 0.5rem;
    }

    h3 {
        font-size: 1.125rem;
    }

    h4,
    h5,
    h6 {
        font-size: 1rem;
    }

    h5 {
        font-weight: $weight-semibold;
    }

    h6 {
        font-weight: $weight-medium;
    }
}
