@import '../config/colors';
@import '../config/sizes';
@import '../mixins/button';
@import '../mixins/sr-only';

.button {
    $self: &;

    @include button-base;
    @include button-style(
        var(--button-background, #{ $c-black }),
        var(--button-border, #{ $c-black }),
        var(--button-color, #{ $c-white }),
        var(--button-background-hover, #{ $c-white }),
        var(--button-border-hover, #{ $c-blue }),
        var(--button-color-hover, #{ $c-blue }),
        var(--button-background-disabled, #{ $c-grey--light }),
        var(--button-border-disabled, #{ $c-grey--light }),
        var(--button-color-disabled, #{ $c-grey })
    );

    p &,
    .content & {
        margin: 0 0 0.5em;

        ~ #{ $self } {
            margin-left: 0.25em;
        }
    }

    &__text {
        display: inline-block;
        letter-spacing: 0.045em;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        & + #{ $self }__icon {
            margin: 0 0 0 12px;
        }
    }

    &__icon {
        display: inline-block;
        margin: 0;
        max-height: 24px;
        max-width: 24px;
        padding: 6px 0 6px 12px;
        vertical-align: 10px;

        &.icon {
            display: inline-block; // Win from icon styles
        }

        & + #{ $self }__text {
            margin-left: 0 12px 0 0;
        }
    }

    &--icon {
        padding: 10px;
        width: var(--button-height);

        #{ $self }__icon {
            left: 50%;
            margin: 0;
            position: absolute;
            top: 50%;
            transform: translate(-50%, -50%);
        }

        span,
        #{ $self }__text {
            @include sr-only;
        }
    }

    &--outline {
        --button-background: transparent;
        --button-border: #{ $c-blue };
        --button-color: #{ $c-blue };
        --button-background-hover: #{ $c-black };
        --button-border-hover: #{ $c-black };
        --button-color-hover: #{ $c-white };
    }
}
