@import '../config/colors';
@import '../config/sizes';
@import '../mixins/container';
@import '../mixins/screen';
@import '../mixins/sr-only';
@import '../mixins/svg';
@import '../mixins/typography';

.article {
    $self: &;

    background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.07) 100%) no-repeat;
    background-size: auto 240px;
    height: 100%;
    padding: 2rem 0;

    &__container {
        @include container($w-content);

        padding: 0 $p-gutter;
        position: relative;
    }

    &__header {
        margin-bottom: 2rem;
        position: relative;
        width: 100%;
    }

    &__title {
        @include style-heading-article;

        color: var(--article-title-color, $c-blue--dark);
        font-weight: $weight-bold;
        hyphens: auto;
        margin: 0;
        word-break: break-word;
    }

    &__meta {
        color: var(--article-meta-color, $c-grey--dark);
        margin: 0 0 0.5rem;
    }

    &__content {
        padding-bottom: 2rem;
    }

    @include screen($screen-simple) {
        padding: 3rem 0;

        &__header {
            margin-bottom: 3rem;
        }
    }
}
