@import '../config/colors';
@import '../config/sizes';
@import '../mixins/object-fit';
@import '../mixins/screen';
@import '../mixins/stretch';

.site {
    $self: &;

    background-color: var(--site-background, $c-white);
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 100vw;
    min-height: 100vh;
    overflow-x: hidden;
    position: relative;
    width: 100%;

    &__background {
        @include stretch;

        background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, $c-white 100%), url('../img/background.png');
        background-position-x: center;
        background-position-y: top;
        background-repeat: no-repeat;
        background-size: cover;
        max-height: 600px;
    }

    &__header {
        position: relative;
        z-index: 2;
    }

    &__content {
        display: flex;
        flex: 1 0 auto;
        flex-wrap: wrap;
        position: relative;
        z-index: 1;

        > * {
            width: 100%;
        }
    }

    &__footer {
        flex: 0 0 auto;
        position: relative;
    }

    &--landingspage {
        #{ $self }__background {
            background-image: url('../img/background.png');
            max-height: none;
        }
    }
}
