@import '../config/colors';
@import '../config/sizes';
@import '../mixins/container';
@import '../mixins/screen';
@import '../mixins/sr-only';

.footer {
    $self: &;

    &__container {
        @include container($w-site);

        display: flex;
        flex-direction: column;
        gap: 2rem;
        padding: 0 $p-gutter;
    }

    &__legal {
        background-color: var(--footer-legal-background, $c-black);
        padding: 2rem 0;
    }

    @include screen($screen-simple) {
        &__container {
            flex-direction: row;
        }

        &__legal {
            align-items: center;
            display: flex;
            height: $h-footer;
            justify-content: center;
        }
    }

    @include screen($screen-normal) {
        &__container {
            align-items: center;
        }
    }

    @include screen($screen-large) {
        &__container {
            padding: 0 $p-gutter--large;
        }
    }
}
